"use client";

import React, { useState, useEffect } from 'react';
import Link from 'next/link';

type Employee = {
  id: string;
  name: string;
  position: string;
  imageUrl: string | null;
  averageRating: number | null;
  reviewCount: number | null;
  active?: boolean;
};

export default function EmployeeFeedbackPage() {
  const [employees, setEmployees] = useState<Employee[]>([]);
  const [selectedEmployee, setSelectedEmployee] = useState('');
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState('');
  const [submitted, setSubmitted] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Fetch employees from API
  const fetchEmployees = async () => {
    setIsLoading(true);
    try {
      const response = await fetch('/api/employees');
      if (!response.ok) throw new Error('Failed to fetch employees');
      const data = await response.json();
      setEmployees(data);
    } catch (err) {
      console.error('Error fetching employees:', err);
      setError('Failed to load employees. Please try again later.');
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchEmployees();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (isSubmitting) return;
    setIsSubmitting(true);
    setError(null);

    try {
      // A temporary user ID for anonymous reviews
      const userId = `anonymous-${Date.now()}`;

      const response = await fetch('/api/reviews', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          employeeId: selectedEmployee,
          rating,
          comment,
          userId,
        }),
      });

      if (!response.ok) throw new Error('Failed to submit feedback');

      setSubmitted(true);
      // Refresh employee data to show new average rating
      fetchEmployees(); 
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An unknown error occurred');
    } finally {
      setIsSubmitting(false);
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 px-4">
        <div className="relative">
          <div className="absolute inset-0 bg-gradient-to-r from-orange-500 to-pink-500 rounded-full blur-2xl opacity-50 animate-pulse"></div>
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-orange-500 relative"></div>
        </div>
        <p className="text-lg text-gray-600 mt-4">Loading employees...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 px-4">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-red-600 mb-4">Oops!</h1>
          <p className="text-lg text-gray-700 mb-8">{error}</p>
          <button 
            onClick={() => window.location.reload()}
            className="bg-orange-600 text-white px-6 py-2 rounded-md hover:bg-orange-700 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  if (submitted) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 px-4">
        <h1 className="text-3xl font-bold text-orange-600 mb-4">Thank you for your feedback!</h1>
        <p className="text-lg text-gray-700 mb-8">We appreciate your review of our team member.</p>
        <Link href="/" className="bg-orange-600 text-white px-6 py-2 rounded-md hover:bg-orange-700 transition-colors">Back to Home</Link>
      </div>
    );
  }

  return (
    <main className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-orange-50 via-pink-50 to-purple-50 px-4 py-12">
      <div className="bg-white/90 rounded-2xl shadow-xl p-8 w-full max-w-md border border-white/20">
        <h1 className="text-3xl font-bold text-center mb-6 bg-gradient-to-r from-orange-600 to-pink-600 bg-clip-text text-transparent">Rate an Employee</h1>
        
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-center mb-4 bg-gradient-to-r from-orange-600 to-pink-600 bg-clip-text text-transparent">Our Team</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
            {employees.map(employee => (
              <div key={employee.id} className="bg-white rounded-2xl shadow-lg p-4 flex flex-col items-center">
                <img src={employee.imageUrl || '/default-avatar.png'} alt={employee.name} className="w-20 h-20 rounded-full object-cover mb-3 border-4 border-pink-200" />
                <div className="font-bold text-lg text-gray-900">{employee.name}</div>
                <div className="text-sm text-gray-500 mb-1">{employee.position}</div>
                <div className="flex items-center gap-1">
                  <StarRating rating={employee.averageRating || 0} />
                  <span className="text-sm text-gray-600 ml-1">
                    ({employee.reviewCount || 0})
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {employees.length === 0 ? (
          <div className="text-center py-8">
            <p className="text-gray-600 mb-4">No employees available for rating at the moment.</p>
            <p className="text-sm text-gray-500">Please check back later!</p>
          </div>
        ) : (
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Select Employee</label>
              <select
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 text-gray-900 bg-white"
                value={selectedEmployee}
                onChange={e => setSelectedEmployee(e.target.value)}
                required
              >
                <option value="">Choose an employee...</option>
                {employees.map(employee => (
                  <option key={employee.id} value={employee.id}>
                    {employee.name} - {employee.position}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Your Rating</label>
              <div className="flex space-x-2">
                {[1,2,3,4,5].map(star => (
                  <button
                    type="button"
                    key={star}
                    className={`text-3xl ${star <= rating ? 'text-yellow-400' : 'text-gray-300'} focus:outline-none hover:scale-110 transition-transform`}
                    onClick={() => setRating(star)}
                    aria-label={`Rate ${star} star${star > 1 ? 's' : ''}`}
                  >
                    â˜…
                  </button>
                ))}
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Comment (optional)</label>
              <textarea
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 text-gray-900 bg-white"
                rows={4}
                value={comment}
                onChange={e => setComment(e.target.value)}
                placeholder="Tell us more about your experience with this team member..."
              />
            </div>
            <button
              type="submit"
              className="w-full bg-gradient-to-r from-orange-600 to-pink-600 hover:from-orange-700 hover:to-pink-700 text-white font-semibold py-3 px-6 rounded-xl shadow-lg transition-all duration-300 hover:scale-105 transform-gpu disabled:opacity-50"
              disabled={!selectedEmployee || rating === 0 || isSubmitting}
            >
              {isSubmitting ? 'Submitting...' : 'Submit Review'}
            </button>
          </form>
        )}
      </div>
    </main>
  );
}

// Helper component for star rating display
const StarRating = ({ rating }: { rating: number }) => (
  <div className="flex">
    {[...Array(5)].map((_, index) => (
      <svg
        key={index}
        className={`w-4 h-4 ${index < Math.round(rating) ? 'text-yellow-400' : 'text-gray-300'}`}
        fill="currentColor"
        viewBox="0 0 20 20"
      >
        <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
      </svg>
    ))}
  </div>
); 